"use client";

import { buttonVariants } from "../ui/button";
import Link from "next/link";
import { RouteLinks } from "./utils/route-links";

export const Header = () => {
  const checkUserSignIn = async () => {
    try {
      const response = await fetch(`/api/auth`);
      console.log(response);
      return response.ok;
    } catch (error) {
      console.error("Error checking user sign-in status:", error);
      return false;
    }
  };

  return (
    <div className="inspect flex h-14 w-[100%] items-center justify-center gap-2 whitespace-nowrap font-geistmono text-sm">
      <RouteLinks />
      <Link
        className={`${buttonVariants({ variant: "default" })} mb-10`}
        href="/route/overview"
      >
        {checkUserSignIn ? "Sign out" : "Sign in"}
      </Link>
    </div>
  );
};
